import { Component, OnInit } from '@angular/core';
import { IUppy, UppyFile } from "uppy-store-ngrx";
import { FormBuilder, FormGroup, Validators } from '@angular/forms';
import { ActivatedRoute, Router, Params } from '@angular/router';
import {Location} from '@angular/common';
import * as Uppy from 'uppy';
import { InvoiceService } from '../services/invoice.service';
@Component({
  selector: 'app-step2',
  templateUrl: './step2.html',
  styleUrls: ['./step2.scss'] 
})
export class Step2Component implements OnInit {
  uploadForm: FormGroup;
  private uppy;
  formData: any;
  isUploadValid: boolean = false;
  pendingPages: Number;

  constructor(
    private _location: Location, 
    private formBuilder: FormBuilder,
    private _invoiceservice: InvoiceService,
    private router: Router,) { }

  ngOnInit() {

    this.formData = history.state.data;
    this.pendingPages = this.formData.invoice_pages || 1;

    // this.uploadForm = this.formBuilder.group({
    //   formData: ['', Validators.required],
    //   pendingPages: ['', Validators.required],
    // });

    this.uppy = Uppy.Core({
      debug: false,
      autoProceed: false,
      restrictions: {
        maxFileSize: 20 * 1000* 1000, // 20 MB
        minNumberOfFiles: 1,
        allowedFileTypes: ['image/*', '.jpg', '.jpeg', '.png']
      }
    })
    .use(Uppy.Dashboard, {
      trigger: ".UppyModalOpenerBtn",
      inline: true,
      target: ".DashboardContainer",
      replaceTargetContent: true,
      note: "Images only up to 20 MB each",
      height: 350,
      hideUploadButton: true,
      metaFields: true,
      disableInformer:true
    })
    .use(Uppy.ProgressBar, {
      target: '.for-ProgressBar', hideAfterFinish: false
    })
    .use(Uppy.Tus, {endpoint: 'https://master.tus.io/files/'});

    this.uppy.
    on('file-added', (file) => {
      this.updateIsUploadValid(this.uppy.getFiles().length);
    }).
    on('file-removed', (file) => {
      this.updateIsUploadValid(this.uppy.getFiles().length);
    });

    // this.uppy.on("complete", result => {
    //   console.log("successful files:", result.successful);
    //   console.log("failed files:", result.failed);
    // });
  }

  backClicked() {
    this._location.back();
  }

  upload() {
    this.uppy.upload().then((res) => {
      if (res.failed.length != 0) {
        window.alert(`Failed to upload ${res.failed.length} files.`);
        return;
      }

      let sub = null;
      if (this.formData.invoice_type == "single") {
        sub = this._invoiceservice.uploadSinglePageInvoices(
          this.formData, res.successful.map(r => r.uploadURL)
        ).toPromise();
      } else {
        sub = this._invoiceservice.uploadMultiPageInvoices(
          this.formData, res.successful.map(r => r.uploadURL)
        ).toPromise();
      }
      
      sub.then(()=>{
        window.alert("Upload Complete");
        this.uppy.reset();
      });

    }).catch(() => {
      window.alert(`Failed to upload all files.`);
    });

  }

  updateIsUploadValid(filesUploadedCount) {
    this.pendingPages = this.formData.invoice_pages - filesUploadedCount;
    this.isUploadValid = this.pendingPages == 0;
  }


}
